'use client';

import { useState } from 'react';
import useSWR from 'swr';
import { useSession } from 'next-auth/react';
import { format } from 'date-fns';
import Link from 'next/link';

interface User {
    id: number;
    tenant_id: number;
    email: string;
    role: 'SYSTEM_ADMIN' | 'ORG_ADMIN' | 'SCHEDULER' | 'VIEWER';
    created_at: string;
}

interface Tenant {
    id: number;
    name: string;
}

const fetcher = (url: string) => fetch(url).then((res) => res.json());

const ROLE_OPTIONS = [
    { value: 'SYSTEM_ADMIN', label: 'System Admin', description: 'Full access to all tenants' },
    { value: 'ORG_ADMIN', label: 'Organization Admin', description: 'Manage their organization' },
    { value: 'SCHEDULER', label: 'Scheduler', description: 'Create and manage events' },
    { value: 'VIEWER', label: 'Viewer', description: 'Read-only access' },
];

export default function UsersPage() {
    const { data: session } = useSession();
    const [selectedTenant, setSelectedTenant] = useState<number>(1);
    const [successMessage, setSuccessMessage] = useState<string | null>(null);

    const isSuperAdmin = session?.user?.role === 'SYSTEM_ADMIN';

    const { data: tenants } = useSWR<Tenant[]>('/api/tenants', fetcher);
    const { data: users, mutate: mutateUsers } = useSWR<User[]>(
        `/api/users?tenant_id=${selectedTenant}`,
        fetcher
    );

    const handleCreateUser = async (e: React.FormEvent<HTMLFormElement>) => {
        e.preventDefault();
        const formData = new FormData(e.currentTarget);

        try {
            const response = await fetch('/api/users', {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({
                    tenant_id: isSuperAdmin ? formData.get('tenant_id') : selectedTenant,
                    email: formData.get('email'),
                    password: formData.get('password'),
                    role: formData.get('role'),
                }),
            });

            if (response.ok) {
                mutateUsers();
                e.currentTarget.reset();
                setSuccessMessage('User created successfully!');
                setTimeout(() => setSuccessMessage(null), 3000);
            } else {
                const data = await response.json();
                alert(data.error || 'Failed to create user');
            }
        } catch (error) {
            console.error('Failed to create user:', error);
            alert('Failed to create user');
        }
    };

    return (
        <div className="min-h-screen bg-slate-50 p-8">
            <div className="max-w-6xl mx-auto">
                {/* Header */}
                <div className="mb-8">
                    <Link href="/admin" className="text-blue-600 hover:text-blue-700 mb-4 inline-block">
                        ‚Üê Back to Admin Dashboard
                    </Link>
                    <h1 className="text-4xl font-bold text-slate-900 mb-2">User Management</h1>
                    <p className="text-slate-600">Create and manage user accounts</p>
                </div>

                {/* Tenant Selector (Super Admin only) */}
                {isSuperAdmin && tenants && tenants.length > 0 && (
                    <div className="mb-6 bg-white rounded-xl shadow p-6">
                        <label className="block text-sm font-medium text-slate-700 mb-2">
                            Select Organization
                        </label>
                        <select
                            value={selectedTenant}
                            onChange={(e) => setSelectedTenant(parseInt(e.target.value))}
                            className="px-4 py-2 border border-slate-300 rounded-lg bg-white"
                        >
                            {tenants.map((tenant) => (
                                <option key={tenant.id} value={tenant.id}>
                                    {tenant.name}
                                </option>
                            ))}
                        </select>
                    </div>
                )}

                {/* Success Message */}
                {successMessage && (
                    <div className="mb-6 bg-green-50 border border-green-200 text-green-800 px-4 py-3 rounded-lg">
                        {successMessage}
                    </div>
                )}

                {/* Create User Form */}
                <div className="bg-white rounded-xl shadow p-6 mb-8">
                    <h2 className="text-2xl font-bold text-slate-900 mb-4">Create New User</h2>
                    <form onSubmit={handleCreateUser}>
                        <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mb-6">
                            {/* Tenant Selection (Super Admin only) */}
                            {isSuperAdmin && (
                                <div>
                                    <label className="block text-sm font-medium text-slate-700 mb-2">
                                        Organization <span className="text-red-500">*</span>
                                    </label>
                                    <select
                                        name="tenant_id"
                                        required
                                        defaultValue={selectedTenant}
                                        className="w-full px-3 py-2 border border-slate-300 rounded-lg"
                                    >
                                        {tenants?.map((tenant) => (
                                            <option key={tenant.id} value={tenant.id}>
                                                {tenant.name}
                                            </option>
                                        ))}
                                    </select>
                                </div>
                            )}

                            {/* Email */}
                            <div>
                                <label className="block text-sm font-medium text-slate-700 mb-2">
                                    Email <span className="text-red-500">*</span>
                                </label>
                                <input
                                    type="email"
                                    name="email"
                                    required
                                    placeholder="user@example.com"
                                    className="w-full px-3 py-2 border border-slate-300 rounded-lg"
                                />
                            </div>

                            {/* Password */}
                            <div>
                                <label className="block text-sm font-medium text-slate-700 mb-2">
                                    Password <span className="text-red-500">*</span>
                                </label>
                                <input
                                    type="password"
                                    name="password"
                                    required
                                    minLength={8}
                                    placeholder="Minimum 8 characters"
                                    className="w-full px-3 py-2 border border-slate-300 rounded-lg"
                                />
                            </div>

                            {/* Role */}
                            <div className="md:col-span-2">
                                <label className="block text-sm font-medium text-slate-700 mb-2">
                                    Role <span className="text-red-500">*</span>
                                </label>
                                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-3">
                                    {ROLE_OPTIONS.map((option) => (
                                        <label
                                            key={option.value}
                                            className="relative flex items-start p-3 border border-slate-200 rounded-lg cursor-pointer hover:bg-slate-50"
                                        >
                                            <input
                                                type="radio"
                                                name="role"
                                                value={option.value}
                                                required
                                                className="mt-0.5 mr-3"
                                            />
                                            <div>
                                                <div className="font-medium text-slate-900">{option.label}</div>
                                                <div className="text-xs text-slate-500">{option.description}</div>
                                            </div>
                                        </label>
                                    ))}
                                </div>
                            </div>
                        </div>

                        <button
                            type="submit"
                            className="bg-blue-600 text-white px-6 py-3 rounded-lg hover:bg-blue-700 font-semibold"
                        >
                            Create User
                        </button>
                    </form>
                </div>

                {/* Users List */}
                <div className="bg-white rounded-xl shadow p-6">
                    <h2 className="text-2xl font-bold text-slate-900 mb-4">Existing Users</h2>

                    {!users || users.length === 0 ? (
                        <p className="text-slate-500 text-center py-8">No users found for this organization.</p>
                    ) : (
                        <div className="overflow-x-auto">
                            <table className="w-full">
                                <thead className="bg-slate-50 border-b border-slate-200">
                                    <tr>
                                        <th className="px-4 py-3 text-left text-sm font-semibold text-slate-700">Email</th>
                                        <th className="px-4 py-3 text-left text-sm font-semibold text-slate-700">Role</th>
                                        <th className="px-4 py-3 text-left text-sm font-semibold text-slate-700">Created</th>
                                    </tr>
                                </thead>
                                <tbody className="divide-y divide-slate-200">
                                    {users.map((user) => (
                                        <tr key={user.id} className="hover:bg-slate-50">
                                            <td className="px-4 py-3 text-sm">{user.email}</td>
                                            <td className="px-4 py-3 text-sm">
                                                <span
                                                    className={`inline-block px-2 py-1 rounded text-xs font-medium ${user.role === 'SYSTEM_ADMIN'
                                                            ? 'bg-purple-100 text-purple-800'
                                                            : user.role === 'ORG_ADMIN'
                                                                ? 'bg-blue-100 text-blue-800'
                                                                : user.role === 'SCHEDULER'
                                                                    ? 'bg-green-100 text-green-800'
                                                                    : 'bg-gray-100 text-gray-800'
                                                        }`}
                                                >
                                                    {user.role.replace('_', ' ')}
                                                </span>
                                            </td>
                                            <td className="px-4 py-3 text-sm text-slate-600">
                                                {format(new Date(user.created_at), 'MMM d, yyyy')}
                                            </td>
                                        </tr>
                                    ))}
                                </tbody>
                            </table>
                        </div>
                    )}
                </div>
            </div>
        </div>
    );
}
